$boxShadow: 0px 8px 20px rgba(67, 116, 229, 0.5),
  0px 4px 10px rgba(255, 255, 255, 0.1);

//---------------------//
$primaryFontManrope: 'Manrope', sans-serif;
$wetherFontRoboto: 'Roboto', sans-serif;

$primaryBorderRadius: 32px;
$cardBorderRadius: 4px;
$paginationBtnBorderRadius: 2px;
$primaryFontSize: 22px;
$primaryLineHeight: calc(44px / 32px);
$secondaryLineHeight: calc(22px / 16px);

//*  */
$durationFunction: 250ms;
$timingFunction: cubic-bezier(0.4, 0, 0.2, 1);
